:root {
  /* Usar nombres de color que comiencen con --color
     se conservan los anteriores nombres por compatibilidad
  */
  --color-first: hsl(207, 84%, 52%);
  --color-first-alt: hsl(210, 84%, 48%);
  --color-second: hsl(192, 82%, 58%);
  --color-second-alt: hsl(195, 82%, 54%);
  --color-third: hsl(6, 87%, 58%);
  --color-third-alt: hsl(7, 87%, 55%);
  --color-fourth: hsl(120, 60%, 58%);
  --color-fourth-alt: hsl(125, 60%, 53%);
  --color-accent: hsl(43, 93%, 52%);
  --color-accent-alt: hsl(41, 93%, 50%);
  --color-light: hsl(0, 0%, 94%);
  --color-dark: hsl(207, 30%, 17%);
  --color-dark-alt: hsl(208, 29%, 10%);
  --color-border: hsl(180, 5%, 88%);
  --color-text: hsl(206, 4%, 34%);
  --color-text-light: hsl(204, 4%, 74%);
  --color-body-bg: hsl(0, 0%, 100%);
