  --first-color-alt: var(--color-first-alt);
  --second-color: var(--color-second);
  --second-color-alt: var(--color-second-alt);
  --third-color: var(--color-third);
  --third-color-alt: var(--color-third-alt);
  --fourth-color: var(--color-fourth);
  --fourth-color-alt: var(--color-fourth-alt);
  --accent-color: var(--color-accent);
  --accent-color-alt: var(--color-accent-alt);
  --light-color: var(--color-light);
  --dark-color: var(--color-dark);
  --dark-color-alt: var(--color-dark-alt);
  --border-color: var(--color-border);
  --text-color: var(--color-text);
  --body-bg: var(--color-body-bg);
  --color: var(--color-first);
  --color-alt: var(--color-first-alt); }

